<template>
	<div class="newaddress">
		<van-address-edit
		  :area-list="areaList"
		  show-postal
		  show-delete
		  show-set-default
		  show-search-result
		  :search-result="searchResult"
		  :area-columns-placeholder="['', '', '']"
		  @save="onSave"
		  @delete="onDelete"
		  @change-detail="onChangeDetail"
		/>
	</div>
</template>

<script>
	import Vue from 'vue';
	import { AddressEdit, } from 'vant';
	
	Vue.use(AddressEdit);
	export default {
		name:'newaddress',
		data(){
			return {
				areaList:
					{
					  province_list: {
					    110000: '',
					    120000: ''
					  },
					  city_list: {
					    110100: '',
					    110200: '',
					    120100: '',
					    120200: ''
					  },
					  county_list: {
					    110101: '',
					    110102: '',
					    110105: '',
					    110106: '',
					    120101: '',
					    120102: '',
					    120103: '',
					    120104: '',
					    120105: '',
					    // ....
					  }
					},
				searchResult: [],
			}
		},
		 methods: {
		    onSave(value) {
				this.$router.push({name:'Address',params:{newAddr:value}})
				console.log(value)
		    },
		    onDelete() {
		      Toast('delete');
		    },
		    onChangeDetail(val) {
		      if (val) {
		        this.searchResult = [
		          {
		            name: '',
		            address: '',
		          },
		        ];
		      } else {
		        this.searchResult = [];
		      }
		    },
		},
	}
</script>

